.destinations {
    @include container;

    padding-block: 72px;

    &__grid {
        margin-top: 64px;

        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 30px;
    }

    &__tour {
        border-radius: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        box-shadow: $main-shadow;
        overflow: hidden;

        &:hover img {
            transform: scale(1.05);
        }

        img {
            width: 100%;
            height: 100%;
            transition: .3s;
            object-fit: cover;
        }

        &_main {
            grid-column: span 2;
            grid-row: span 2;

            h3 {
                color: $text-extra;
                font-family: "Comfortaa";
                font-size: 80px;
                font-weight: 700;
                line-height: 120%;

                position: absolute;
                left: 32px;
                top: 50%;

                transform: translate(0, -50%);
            }

            p {
                color: $text-extra;
                font-family: "Ubuntu";
                font-size: 32px;
                font-style: normal;
                font-weight: 500;
                line-height: normal;

                position: absolute;
                bottom: 32px;
                right: 48px;

                span {
                    margin-right: 12px;
                }
            }

            .destinations__tour-descr {
                width: 100%;
                height: 100%;
                left: 0;
                bottom: 0;
                top: 0;
                right: 0;
            }
        }

        &-descr {
            position: absolute;
            left: 24px;
            bottom: 32px;
        }

        &-title {
            font-family: "Comfortaa";
            font-size: 36px;
            font-weight: 700;
            color: $text-extra;
        }

        &-location {
            color: $text-extra;
            font-family: "Ubuntu";
            font-size: 24px;
            font-weight: 500;
            margin-top: 6px;

            span {
                margin-right: 8px;
            }
        }
    }
}
