.footer {
    background: $extra;

    &__content {
        @include container;
    }

    &__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include container;

        padding-block: 32px 48px;

        &-logo {
            width: 260px;
            height: auto;

            img {
                width: 100%;
                height: 100%;
            }
        }

        &-list {
            display: flex;
            align-items: center;
        }

        &-item {
            &:not(:last-of-type) {
                margin-right: 32px;
            }
        }

        &-link {
            color: $text-extra;
            font-family: "Ubuntu";
            font-size: 24px;
            font-weight: 400;
            transition: 0.2s;

            &.active {
                color: $text-extra_active;
            }

            &:not(.active):hover {
                color: $text-extra_hover;
            }
        }
    }

    &__contacts {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include container;
        &__icon {
            transition: 0.2s;
        }

        &-link {
            color: $text-extra;
            font-family: "Montserrat";
            font-size: 26px;
            font-weight: 500;
            transition: 0.2s;

            display: flex;
            align-items: center;

            &:hover {
                color: $text-extra_hover;

                span::before {
                    color: $text-extra_hover;
                }
            }
        }

        &-icon {
            font-size: 36px;
            margin-right: 12px;

            &::before {
                color: $text-extra;
            }
        }
    }

    &__details {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include container;
        padding-block: 48px;
    }

    &__socials {
        display: flex;
        align-items: center;

        &-link {
            font-size: 48px;

            &:hover::before {
                color: $text-extra_hover;
            }

            &::before {
                background: none;
                color: $text-extra;
                background-clip: border-box;
                -webkit-background-clip: border-box;
                -webkit-text-fill-color: inherit;
            }

            &:not(:last-of-type) {
                margin-right: 32px;
            }
        }
    }

    &__payment {
        display: flex;
        align-items: center;

        &-card {
            &:not(:last-of-type) {
                margin-right: 32px;
            }

            width: 100px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: $text-extra;
            border-radius: 8px;
        }
    }

    &__copyright {
        padding-block: 32px;

        border-top: 1px solid $text-extra;

        &-text {
            color: #ffffff80;
            font-family: "Montserrat";
            font-size: 24px;
            font-weight: 400;

            @include container;
        }
    }
}
