.why {
    @include container;
    padding-block: 72px;

    &__text {
        margin-top: 24px;
        font-family: "Ubuntu";
        font-size: 24px;
        font-weight: 300;
        line-height: 120%;
        color: $text;
        width: 100%;
        margin-bottom: 48px;
        text-align: justify;
    }

    &__pictures {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;

        img {
            width: 300px;
            height: 420px;
            border-radius: 32px;
        }
    }
}
