.trends {
    @include container;
    padding-top: 72px;

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
    }

    &__card {
        padding: 12px;
        border-radius: 16px;
        border: 2px solid $light-button;
        transition: .2s;

        box-shadow: $extra-shadow;

        background: $main;

        img {
            width: 100%;
            height: 100%;
        }

        &:hover {
            transform: scale(.99);
            box-shadow: $main-shadow
        }
    }

    &__btn {
        @extend %button;
        margin-top: 32px;
    }
}