.wrapper {
  background: #fafafa;
  font-family: "Ubuntu";
  z-index: 1;
}
.wrapper *::before,
.wrapper *::after {
  font-family: "FontAwesome";
}

.icon-film::before {
  content: "\f008";
  color: #FFFFFF;
  font-weight: 300;
  transition: 0.2s;
}
.icon-tel::before {
  content: "\f879";
  color: #292929;
  font-weight: 300;
  transition: 0.2s;
}
.icon-mail::before {
  content: "\f0e0";
  color: #FFFFFF;
  font-weight: 300;
}
.icon-location::before {
  content: "\f3c5";
  color: #FFFFFF;
  font-weight: 300;
}
.icon-youtube::before {
  content: "\f167";
  background: linear-gradient(90deg, #8D75FF 0%, #4C1FAF 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 400;
  transition: 0.2s;
}
.icon-instagram::before {
  content: "\f16d";
  background: linear-gradient(90deg, #8D75FF 0%, #4C1FAF 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 400;
  transition: 0.2s;
}
.icon-telegram::before {
  content: "\f2c6";
  background: linear-gradient(90deg, #8D75FF 0%, #4C1FAF 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 400;
  transition: 0.2s;
}
.icon-scope::before {
  content: "\f065";
  color: #FFFFFF;
  font-weight: 300;
}
.icon-arrowTop::before {
  content: "\f077";
  color: #FFFFFF;
  font-weight: 300;
}

