.help__form button {
  color: #FFFFFF;
  font-family: "Comfortaa";
  font-size: 24px;
  font-weight: 600;
  padding: 16px 24px;
  border: 1.5px solid #FFFFFF;
  background: #7E64F9;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.2s linear;
}
.help__form button:hover {
  background: #8d75ff;
  color: rgba(255, 255, 255, 0.8156862745);
  border-color: #FFFFFF;
}
.help__form input {
  padding: 16px 24px;
