}

.gallery__button, .trends__btn {
  padding: 16px 40px;
  background: rgba(92, 0, 209, 0.6);
  color: #FFFFFF;
  font-family: "Ubuntu";
  font-size: 32px;
  font-weight: 500;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  margin: 0 auto;
  transition: 0.2s linear;
}
.gallery__button:hover, .trends__btn:hover {
  opacity: 0.8;
}

.scrollTop {
  position: fixed;
  z-index: 3;
  bottom: 48px;
  right: 48px;
  cursor: pointer;
  width: 64px;
  height: 64px;
  background: #9D66E4;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.6039215686);
  border-radius: 50%;
  transition: 0.2s;
  visibility: hidden;
  opacity: 0;
  transform: translateY(150px);
}
.scrollTop.show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.scrollTop:hover {
  transform: scale(1.1);
}
.scrollTop span {
  font-size: 32px;
  color: #FFFFFF;
}

.header {
  background: #eeeeee;
}
.header__promo {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
  position: relative;
  z-index: 1;
  background: linear-gradient(90deg, #6B59C1 0%, #5D2CCA 31%, #8F0ACE 62%, #755DC1 100%);
}
.header__promo::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #6B59C1 0%, #5D2CCA 31%, #8F0ACE 62%, #755DC1 100%);
  z-index: -1;
  transition: width 0.6s linear;
}
.header__promo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: linear-gradient(90deg, #8F0ACE 15%, #5D2CCA 33%, #755DC1 68%, #6B59C1 100%);
  z-index: -1;
  transition: width 0.6s linear;
}
.header__promo:hover::after {
  width: 100%;
}
.header__promo:hover::before {
  width: 0;
}
.header__promo-icon {
  font-size: 34px;
  margin-right: 16px;
  transition: color 0.2s;
}
.header__promo:hover .header__promo-icon::before {
  color: rgba(255, 255, 255, 0.8156862745);
}
.header__promo-text {
  color: #FFFFFF;
  font-family: "Montserrat";
  font-size: 22px;
  font-weight: 500;
  transition: color 0.2s;
}
.header__promo:hover .header__promo-text {
  color: rgba(255, 255, 255, 0.8156862745);
}
